
          @import '@/assets/sass/variables.scss';
          @import '@/assets/sass/mixins.scss';
        

.dashboard-link {
  .activity-icons {
    line-height: 1;
  }

  .dashboard-link-main {
    flex-grow: 1;
    padding-left: 3px;
    padding-right: 3px;
  }

  .document-title:after {
    content: '\0000a0\002022\0000a0'; //&nbsp;&bull;&nbsp;
  }

  .area-title:not(:last-child):after {
    content: ', ';
  }
}

.dashboard-link:visited {
  color: $grey-light;
}
